<template>
  <div>
    <!--面包屑导航-->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href>首页</a>&gt;
          <a href>公司动态</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="news_details" v-if="data">
        <h2>{{data.title}}</h2>
        <span>发布时间:{{data.pubTime | date}}</span>
        <div class="news_content" v-html="transformHTML(data.content)"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: null,
    };
  },
  methods: {
    transformHTML(html) {
      // <img src="news_imgs/01.jpg" alt="">
      // <img src="http://101.96.128.94:9999/mfresh/news_imgs/01.jpg" alt="">
      return html.replace(
        /<img src="news_imgs/g,
        '<img src="http://101.96.128.94:9999/mfresh/news_imgs'
      );
    },
  },
  mounted() {
    let nid = this.$route.params.nid;
    this.axios
      .get("news_detail.php?nid=" + nid)
      .then((res) => {
        // console.log(res);
        this.data = res.data;
      })
      .catch((err) => {
        console.error(err);
      });
  },
};
</script>

